<template>
<el-card>
    <div slot="header">
        <span>订单管理</span>
    </div>
    <!-- 搜索 -->
    <div>
        <el-form :model="objSearch" :rules="rules" ref="formSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="9">
                    <el-form-item label="服务器" prop="serverId">
                        <el-select v-model="objSearch.serverId" placeholder="请选择服务器">
                            <el-option v-for="(ele,idx) in aServerList" :label="ele.name" :value="ele.serverId" :key="idx"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="角色ID" prop="pid">
                        <el-input v-model="objSearch.pid" placeholder="请输入角色ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-button :disabled="!objSearch.pid" type="primary" @click="handleSearch('formSearch')" style="marginRight:20px;">搜索</el-button>
                    <el-button type="primary" @click="handleClickDownload()">下载</el-button>
                    <el-button type="primary" @click="handleReset('formSearch')">重置</el-button>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-form-item label="开始" prop="start">
                        <el-date-picker v-model="objSearch.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始时间"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="结束" prop="end">
                        <el-date-picker v-model="objSearch.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束时间"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <!-- 合计 -->
    <el-row>
        <p style="lineHeight:32px;">【总记录数：{{objSummary.totalNum}}】-【总角色数：{{objSummary.roleNum}}】-【充值总额：{{objSummary.moneyNum}}】</p>
    </el-row>
    <!-- 表格 -->
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%" max-height="600">
            <el-table-column prop="id" label="游戏订单id" min-width="120"></el-table-column>
            <el-table-column prop="pid" label="角色id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="serverId" label="游戏服id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="instanceId" label="实例id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="userId" label="第三方会员id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="orderNo" label="第三方订单id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="payTime" label="支付时间" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="payMoney" label="支付金额" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="支付状态" min-width="120" show-overflow-tooltip>
                <template slot-scope="scope">
                    <p>{{scope.row.orderStatus | statusFilter}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="subject" label="订单名称" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="extensionInfo" label="拓展信息" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="payId" label="商品id" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="订单处理状态" min-width="120" show-overflow-tooltip>
                <template slot-scope="scope">
                    <p>{{scope.row.handleFlag | flagFilter}}</p>
                </template></el-table-column>
            <el-table-column prop="handleTime" label="订单处理时间" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="callBackTime" label="回调时间" min-width="120" show-overflow-tooltip></el-table-column>
            <el-table-column prop="createTime" label="创建时间" min-width="120" show-overflow-tooltip></el-table-column>
        </el-table>
    </div>
</el-card>
</template>

<script>
import axios from "axios";
import baseURL from "../config/baseUrl";
export default {
    name: "order",
    components: {},
    props: {},
    data() {
        return {
            aServerList: [],
            objSummary: {
                totalNum: 0,
                roleNum: 0,
                moneyNum: 0
            },
            objSearch: {
                serverId: '',
                pid: '',
                start: '',
                end: '',
            },
            aTableData: [],
            rules: {
                serverId: [{
                    required: true,
                    message: "请选择",
                    trigger: "change"
                }],
                pid: [{
                    required: true,
                    message: "请输入",
                    trigger: "blur"
                }],
                start: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                end: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
            }
        };
    },
    watch: {},
    filters: {
        statusFilter(status) {
            const statusMap = ["等待支付", "支付成功", "支付失败", "支付中", "支付金额错误", "支付渗透参数错误", "支付项错误"];
            return statusMap[Number(status)];
        },
        flagFilter(status) {
            const statusMap = ["初始化", "发货成功", "代发货"];
            return statusMap[Number(status)];
        }
    },
    methods: {
        // 重置
        handleReset(formName) {
            this.$refs[formName].resetFields();
        },
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员';
        },
        //   搜索
        handleSearch(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.fetchList();
                } else {
                    return false;
                }
            });
        },
        // 下载
        handleClickDownload() {
            if (this.objSearch.serverId) {

                let params = JSON.parse(JSON.stringify(this.objSearch))
                axios({
                    method: "post",
                    headers: {
                        "content-type": "application/json; charset=utf-8"
                    },
                    responseType: "stream",
                    url: baseURL + API.order.download_order,
                    data: params
                }).then(response => {
                    let dataBlob = new Blob([`\ufeff${response.data}`], {
                        type: "text/plain;charset=utf-8"
                    });
//                    var dataBlob = response.data;
                    const url = window.URL.createObjectURL(dataBlob);
                    const a = document.createElement("a");
                    a.href = url;
                    // var fileName = 'sdf.csv';
                    var fileName = decodeURIComponent(response.headers["content-disposition"].split(";")[1].split("filename=")[1].split('.')[0]);
                    a.download = `${fileName}.csv`;
                    a.click();
                    window.URL.revokeObjectURL(url);
                });
            } else {
                this.$message.error('下载前请选择服务器！')
            }
        },
        // 列表
        fetchList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            this.$fetch.post(API.order.select_order, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let result = res.data;
                    let count = result.length,
                        roles = [],
                        newRoles = [],
                        nMoney = 0;
                    result.forEach(item => {
                        let newMoney = Number(item.payMoney) || 0;
                        nMoney += newMoney;
                        roles.push(item.pid)
                    })
                    newRoles = Array.from(new Set(roles))
                    this.objSummary = {
                        totalNum: count,
                        roleNum: newRoles.length,
                        moneyNum: nMoney
                    }
                    this.aTableData = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchServerList() {
            this.$fetch.post(API.email.server_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aServerList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchServerList();
        // this.fetchList();
    }
};
</script>

<style lang="stylus" scoped>
</style>
